var defaultState=Object(__WEBPACK_IMPORTED_MODULE_1_immutable__["fromJS"])({title:'衡水中学，被外地人占领的高考工厂',content:'<div class="article-content"><div class="img-container">\n   </div><p><span class="bjh-p">\n    2017\u5E74\uFF0C\u8861\u6C34\u4E2D\u5B66\u8003\u4E0A\u6E05\u534E\u5317\u5927\u7684\u4EBA\u6570\u662F176\u4EBA\uFF0C2016\u5E74\u662F139\u4EBA\uFF0C\u518D\u5F80\u524D\u63A8\u52302015\u5E74\uFF0C\u8FD9\u4E2A\u4EBA\u6570\u662F119\u4EBA\u3002\u4F46\u662F\u5728\u8FD9\u4E9B\u8F89\u714C\u7684\u540D\u5355\u80CC\u540E\uFF0C\u5374\u662F\u5916\u5730\u6765\u8861\u6C34\u4E0A\u5B66\u4EBA\u6570\u66B4\u6DA8\uFF0C\u672C\u5730\u4EBA\u4E0A\u597D\u9AD8\u4E2D\u8D8A\u6765\u8D8A\u8270\u96BE\u7684\u5C34\u5C2C\u5904\u5883\u3002\n    </span></p><p><span class="bjh-p">\u201C\n    <span class="bjh-strong">\u8861\u6C34\u7684\u9AD8\u4E2D\u57FA\u672C\u4E0A80%\u4EE5\u4E0A\u90FD\u662F\u5916\u5730\u5B69\u5B50\u5728\u4E0A\uFF0C\u672C\u5730\u7684\u5B69\u5B50\u6839\u672C\u6CA1\u5730\u65B9\u53BB\u4E0A\u5B66</span>\u201D\uFF0C\n    \u754C\u9762\u65B0\u95FB\u5728\u300A\u4E89\u8BAE\u4E2D\u7684\u8861\u6C34\u7CFB\u201C\u8D85\u7EA7\u4E2D\u5B66\u201D\uFF1A\u62E9\u6821\u5927\u6218\u8513\u5EF6\u81F3\u201C\u5C0F\u5347\u521D\u201D\u300B\u4E2D\u6240\u8A00\uFF0C\u5E76\u4E0D\u53EA\u662F\u4E00\u53E5\u7A7A\u8BDD\u3002</span></p><p>\n    <span class="bjh-h3">\u4E0D\u89C4\u8303\u62DB\u751F\uFF0C\u6390\u5C16\u5916\u5730\u4F18\u8D28\u751F\u6E90</span></p><p><span class="bjh-p">\n    \u8861\u6C34\u4E3B\u8981\u7684\u9AD8\u4E2D\u6709\u516C\u529E\u7684\u8861\u6C34\u4E2D\u5B66\u3001\u8861\u6C34\u4E8C\u4E2D\u3001\u5341\u4E09\u4E2D\u3001\u5341\u56DB\u4E2D\uFF0C\u6C11\u529E\u7684\u8861\u6C34\u4E00\u4E2D\u3001\u5FD7\u81FB\u4E2D\u5B66\u3002</span></p><p><span class="bjh-p">\n    \u4EE5\u5F80\u5728\u672C\u5730\u4EBA\u773C\u91CC\uFF0C\u5341\u4E09\u4E2D\u3001\u5341\u56DB\u4E2D\u90FD\u662F\u76F8\u5BF9\u4E00\u822C\u7684\u5B66\u6821\uFF0C\u5206\u6570\u7EBF\u4E5F\u6BD4\u8F83\u4F4E\uFF0C\u5982\u679C\u5206\u6570\u4E0D\u591F\u7684\u8BDD\uFF0C\u4EA4\u51E0\u5343\u5757\u94B1\u5C31\u80FD\u591F\u62A5\u540D\uFF0C\u4F46\u662F\u73B0\u5728\u56E0\u4E3A\u8861\u4E2D\u3001\u4E8C\u4E2D\u7684\u4EBA\u6570\u7206\u6EE1\uFF0C\u8FD9\n    \u4E24\u6240\u5B66\u6821\u4E5F\u53D8\u5F97\u6781\u5176\u62A2\u624B\u3002</span></p><div class="img-container">\n    </div><p>\n    \u8861\u6C34\u5341\u4E09\u4E2D</p><p><span class="bjh-p"><span class="bjh-strong">2017\u5E74\uFF0C\u5341\u4E09\u4E2D\u7684\u5206\u6570\u7EBF\u63D0\u9AD8\u5230\u4E86500\u591A\u5206\uFF0C\u4F4E\u4E8E\u8FD9\u4E00\u6210\u7EE9\u7684\u5C5E\u4E8E\u81EA\u8D39\u751F\uFF0C\u6BCF\u5E74\n    \u5B66\u8D39\u9AD8\u8FBE9000\u5143\u3002</span></span></p><p><span class="bjh-p"><span class="bjh-strong">\u5341\u56DB\u4E2D\u7684\u5206\u6570\u7EBF\uFF0C\u5219\u4F1A\u968F\u7740\n    \u62A5\u540D\u7684\u4EBA\u6570\u3001\u62A5\u540D\u4EBA\u7684\u5206\u6570\u800C\u53D8\u5316\u3002</span>\u5F20\u5B87\u521D\u4E2D\u662F\u5728\u8861\u6C34\u53BF\u91CC\u4E0A\u7684\uFF0C\u672C\u6765\u5982\u679C\u4E2D\u8003\u7684\u65F6\u5019\u80FD\u591F\u8003\u4E0A\u5E74\u7EA7\u524D\u591A\u5C11\u540D\uFF0C\u5C31\u4F1A\u6709\u4E0A\u8861\u4E2D\u7684\u673A\u4F1A\uFF0C\n    \u4F46\u5979\u90A3\u5E74\u6CA1\u53D1\u6325\u597D\uFF0C\u6CA1\u80FD\u8003\u4E0A\uFF0C\u6700\u540E\u53EA\u80FD\u62A5\u8003\u5341\u56DB\u4E2D\u3002\u5BB6\u91CC\u4EBA\u4E3A\u4E86\u8D76\u7D27\u7ED9\u5979\u201C\u5360\u4F4F\u201D\u4E00\u4E2A\u540D\u989D\uFF0C\u9876\u7740\u5927\u592A\u9633\u5728\u5341\u56DB\u4E2D\u95E8\u53E3\u6392\u4E86\u4E00\u5929\u961F\uFF0C\u7EC8\u4E8E\u6210\u529F\u4EA4\u4E0A\n    \u4E86\u5B66\u8D39\u62A5\u4E0A\u4E86\u540D\uFF0C\u6BCF\u5E74\u5C06\u8FD11\u4E07\u5757\u94B1\uFF0C\u5373\u4FBF\u8FD9\u6837\uFF0C\u7B2C\u4E8C\u5929\u548C\u5979\u540C\u4E00\u4E2A\u5206\u6570\u7EBF\u7684\u4EBA\u5C31\u5DF2\u7ECF\u8FDE\u5341\u56DB\u4E2D\u90FD\u4E0A\u4E0D\u4E86\u4E86\u3002</span></p><p><span class="bjh-p">\u5979\u7684\u670B\u53CB\u7F57\n    \u65B0\u662F\u4E2A\u827A\u672F\u751F\uFF0C\u6210\u7EE9\u6BD4\u8F83\u4E00\u822C\uFF0C\u4E2D\u8003\u5B8C\u4E86\u4E4B\u540E\uFF0C\u5728\u8861\u6C34\u6839\u672C\u6CA1\u5B66\u53EF\u4E0A\uFF0C\u6700\u540E\u53EA\u80FD\u5230\u53BF\u91CC\u9762\u53BB\u5FF5\u4E66\u3002<span class="bjh-strong">\u674E\u827A\u5219\u4E3A\u4E86\u5C3D\u53EF\u80FD\u589E\u52A0\u9AD8\u4E2D\u4E0A\n    \u8861\u4E2D\u7684\u673A\u4F1A\uFF0C\u672C\u6765\u662F\u8861\u6C34\u5E02\u533A\u7684\u4EBA\uFF0C\u521D\u4E2D\u5374\u53BB\u4E86\u53BF\u91CC\u9762\u4E0A\u5B66\u3002</span></span></p><div class="img-container"></div><p>\u8861\u6C34\u4E2D\u5B66\u672C\u90E8\u6821\u95E8</p>\n    <div class="img-container"></div><p>\u7531\u8861\u6C34\u4E2D\u5B66\u3001\u6CF0\u534E\u516C\u53F8\u5408\u4F5C\u65B0\u5EFA\u7684\u6C11\u529E\u9AD8\u7EA7\u4E2D\u5B66\u2014\u2014\u201C\u8861\u6C34\u7B2C\u4E00\u4E2D\u5B66\u201D</p><p><span class="bjh-p">\u65B0\u95FB\u91CC\u6700\u77E5\u540D\u7684\u8861\u6C34\u4E2D\u5B66\u548C\u8861\u6C34\u4E00\u4E2D\uFF0C\u4E0D\u5C11\u5916\u5730\u4EBA\u90FD\u4EE5\u4E3A\u5B83\u4EEC\u662F\u4E00\u6240\u5B66\u6821\uFF0C\u4F7F\u7528\u7684\u65F6\u5019\u90FD\u662F\u6DF7\u6742\u7740\u6765\uFF0C\u5176\u5B9E\u5C31\u7B97\u8861\u6C34\u5F53\u5730\uFF0C\u4E0D\u7BA1\u662F\u5728\u62A5\u7EB8\u4E0A\u7684\u62A5\u9053\uFF0C\u8FD8\u662F\u65E5\u5E38\u95F2\u804A\u3001\u6253\u8F66\uFF0C\u4E5F\u5F80\u5F80\u76F4\u63A5\u5C06\u8861\u6C34\u4E00\u4E2D\u79F0\u4E3A\u201C\u5357\u6821\u533A\u201D\uFF0C\u4F46\u5176\u5B9E\u4E24\u6240\u5B66\u6821\u662F\u6709\u5F88\u5927\u5DEE\u522B\u7684\u2014\u2014</span></p><p><span class="bjh-p">\u8861\u6C34\u4E00\u4E2D\u662F\u8861\u6C34\u4E2D\u5B66\u4E0E\u6CB3\u5317\u6CF0\u534E\u5730\u4EA7\u96C6\u56E2\u8054\u5408\u5EFA\u8BBE\u7684\uFF0C\u4E8E2014\u5E748\u6708\u6B63\u5F0F\u6295\u5165\u4F7F\u7528\uFF0C\u4E5F\u5C31\u662F\u8BF4\uFF0C\u8861\u6C34\u4E00\u4E2D\u5176\u5B9E\u662F\u4E00\u6240\u6C11\u529E\u5B66\u6821\u3002</span></p><p><span class="bjh-p">\u4E0D\u8FC7\uFF0C\u8861\u6C34\u4E2D\u5B66\u548C\u8861\u6C34\u4E00\u4E2D\u4E4B\u95F4\uFF0C\u6559\u5B66\u98CE\u683C\u662F\u4E00\u6837\u7684\uFF0C\u8001\u5E08\u4EEC\u4E5F\u662F\u4E92\u901A\u7684\uFF0C\u751A\u81F3\u4E8E\u5728\u4ECA\u5E746\u6708\u4EFD\u4E4B\u524D\uFF0C\u4E24\u6240\u5B66\u6821\u7684\u6821\u957F\u4E5F\u90FD\u662F\u5F20\u6587\u8302\u3002</span></p><p><span class="bjh-p">\u8FD9\u79CD\u201C\u516C\u79C1\u7ED3\u5408\u201D\u7684\u529E\u5B66\u6A21\u5F0F\uFF0C\u5728\u8861\u6C34\u5E76\u975E\u53EA\u6709\u8861\u4E2D\u4E00\u5BB6\uFF0C\u540C\u6837\u77E5\u540D\u7684\u8861\u6C34\u4E8C\u4E2D\u4E5F\u662F\u7C7B\u4F3C\u64CD\u4F5C\uFF0C\u5B83\u65D7\u4E0B\u7684\u5FD7\u81FB\u4E2D\u5B66\u662F\u4E8C\u4E2D\u548C\u6CB3\u5317\u6CF0\u534E\u5730\u4EA7\u96C6\u56E2\u8054\u5408\u5EFA\u8BBE\u7684\u3002</span></p><div class="img-container">\n    </div><p>\u8861\u6C34\u4E8C\u4E2D</p><p><span class="bjh-p">\u4E4B\u6240\u4EE5\u5982\u6B64\u64CD\u4F5C\uFF0C\u662F\u56E0\u4E3A\u8861\u4E2D\u3001\u4E8C\u4E2D\u5C5E\u4E8E\u516C\u529E\u9AD8\u4E2D\uFF0C\u6309\u7167\u89C4\u5B9A\u53EA\u80FD\u62DB\u6536\u8861\u6C34\u6237\u53E3\u7684\u5B66\u751F\uFF0C\u800C\u8861\u6C34\u4E00\u4E2D\u3001\u5FD7\u81FB\u4E2D\u5B66\u5C5E\u4E8E\u6C11\u529E\u9AD8\u4E2D\uFF0C<span class="bjh-strong">\u4E00\u65B9\u9762\u53EF\u4EE5\u7528\u6765\u89C4\u907F\u7981\u6B62\u8DE8\u533A\u57DF\u62DB\u751F\u7684\u89C4\u5B9A\uFF0C\u5230\u6CB3\u5317\u7701\u7684\u5404\u4E2A\u57CE\u5E02\u3001\u751A\u81F3\u5176\u4ED6\u7701\u4EFD\u62DB\u6536\u4F18\u8D28\u751F\u6E90\uFF0C\u53E6\u4E00\u65B9\u9762\u8FD8\u53EF\u4EE5\u5BF9\u5916\u5730\u5B66\u751F\u3001\u590D\u8BFB\u751F\u6536\u53D6\u9AD8\u989D\u5B66\u8D39\u3002</span></span></p><div class="img-container">\n  </div><p>\u6CB3\u5317\u7701\u6559\u80B2\u5385\u51FA\u53F0\u76F8\u5173\u653F\u7B56\u6CBB\u7406\u8861\u6C34\u4E2D\u5B66\u3001\u8861\u6C34\u7B2C\u4E00\u4E2D\u5B66\u4E0D\u89C4\u8303\u62DB\u751F</p><p><span class="bjh-p">2017\u5E74\uFF0C\u77F3\u5BB6\u5E84\u5E02\u3001\u4FDD\u5B9A\u5E02\u3001\u90AF\u90F8\u5E02\u3001\u90A2\u53F0\u5E02\u6559\u80B2\u5C40\u5206\u522B\n  \u8981\u6C42\uFF1A\u5916\u5730\u6C11\u529E\u9AD8\u4E2D\uFF0C\u672A\u7ECF\u6279\u51C6\u4E00\u5F8B\u4E0D\u5F97\u79C1\u81EA\u8FDB\u5165\u8F96\u533A\u62DB\u751F\uFF0C\u8FDD\u8005\uFF0C\u6559\u80B2\u5C40\u4E0D\u4E88\u529E\u7406\u521D\u4E2D\u5B66\u7C4D\u6863\u6848\u8F6C\u79FB\u624B\u7EED\u3002\u4F46\u662F\u516C\u544A\u5E74\u5E74\u53D1\uFF0C\u5374\u4F9D\u7136\u963B\u6321\u4E0D\u4F4F\u5B66\u751F\u6D41\u5165\u8861\u6C34\u3002\n  </span></p><p><span class="bjh-h3">\u4E0A\u8861\u4E2D\u96BE\uFF0C\u5BB6\u957F\u4EEC\u88AB\u8FEB\u5C06\u76EE\u6807\u7784\u51C6\u5C0F\u5347\u521D</span></p><p><span class="bjh-p">\u968F\u7740\u8861\u6C34\u7CFB\u9AD8\u4E2D\u8D8A\u6765\n  \u8D8A\u62A2\u624B\uFF0C\u5B66\u8D39\u8D8A\u6765\u8D8A\u9AD8\uFF0C\u538B\u529B\u5DE8\u5927\u7684\u5BB6\u957F\u4EEC\u751A\u81F3\u5DF2\u7ECF\u5F00\u59CB\u5C06\u76EE\u6807\u7784\u51C6\u5230\u4E86\u201C\u5C0F\u5347\u521D\u201D\u7684\u9636\u6BB5\uFF0C\u771F\u6B63\u8D2F\u5F7B\u4E86\u201C\u4ECE\u5A03\u5A03\u6293\u8D77\u201D\u7684\u7CBE\u795E\u3002\u4E0D\u5C11\u5916\u5730\u7684\u4EBA\uFF0C\u4E5F\u4F1A\u5728\u521D\u4E2D\u9636\u6BB5\u5C31\n  \u628A\u81EA\u5DF1\u7684\u5B69\u5B50\u9001\u5230\u8861\u6C34\uFF0C\u65B9\u4FBF\u4E4B\u540E\u4E0A\u8861\u4E2D\u6216\u8005\u4E8C\u4E2D\u3002</span></p><p><span class="bjh-p">\u6843\u57CE\u4E2D\u5B66\uFF0C\u8861\u6C34\u4EBA\u4E00\u822C\u4E60\u60EF\u6027\u5730\u5C06\u5B83\u79F0\u4E3A\u8861\u6C34\u4E94\u4E2D\u30022014\u5E74\n  8\u6708\uFF0C<span class="bjh-strong">\u4E94\u4E2D\u521D\u4E2D\u90E8\u4E0E\u8861\u6C34\u4E00\u4E2D\u8054\u529E\u521D\u4E2D\u6210\u7ACB\uFF0C\u56E0\u4E3A\u4E0E\u8861\u4E2D\u6709\u5408\u4F5C\uFF0C\u6240\u4EE5\u6BCF\u5E74\u8861\u4E2D\u7ED9\u7684\u540D\u989D\u4E5F\u8981\u8FDC\u9AD8\u4E8E\u5176\u4ED6\u5B66\u6821\u3002\n  </span></span></p><div class="img-container">\n</div><p>\u8861\u4E2D\u4E94\u4E2D\u6BD5\u4E1A\u5178\u793C</p><p><span class="bjh-p">2017\u5E74\uFF0C\u8861\u6C34\u4E2D\u5B66\u9762\u5411\u5168\u5E02\u5171\u62DB\u751F\u516C\u8D39\u751F990\u4EBA\uFF0C\u5176\u4E2D\u7ED9\u4E94\u4E2D\u4E00\u6240\u5B66\u6821\u7684\u540D\u989D\u5C31\u6709253\u4E2A\uFF0C\u5269\u4E0B\n\u7684\u5B66\u6821\u91CC\uFF0C\u8861\u6C34\u4E09\u4E2D108\u4E2A\u3001\u8861\u6C34\u56DB\u4E2D69\u4E2A\u3001\u8861\u6C34\u516D\u4E2D240\u4E2A\u3001\u8861\u6C34\u4E03\u4E2D87\u4E2A\u3001\u8861\u6C34\u516B\u4E2D31\u4E2A\u3001\u8861\u6C34\u4E5D\u4E2D63\u4E2A\u3001\u8D75\u5708\u4E2D\u5B6637\u4E2A\u3001\u82F1\u624D\u5B66\u682124\u4E2A\uFF0C\u8FD8\u6709\u4E00\u4E9B\u5B66\u6821\uFF0C\u540D\u989D\n\u4E0D\u8DB320\u4E2A\u3002</span></p><p><span class="bjh-p"><span class="bjh-strong">\u4E5F\u6B63\u662F\u56E0\u6B64\uFF0C\u5728\u4EBA\u4EEC\u773C\u4E2D\uFF0C\u201C\u53EA\u8981\u8FDB\u4E86\u4E94\u4E2D\uFF0C\u57FA\u672C\u4E0A\u53EF\u4EE5\u786E\u4FDD\u4E00\u53EA\u811A\u8E0F\u5165\u4E86\u8861\n\u6C34\u540D\u6821\uFF1B\u8E0F\u5165\u4E86\u8861\u6C34\u540D\u6821\uFF0C\u57FA\u672C\u4E0A\u53EF\u4EE5\u786E\u4FDD\u4E00\u53EA\u811A\u8FDB\u5165\u4E86\u540D\u724C\u5927\u5B66\u3002\u201D</span></span></p><p><span class="bjh-p">\u66F4\u91CD\u8981\u7684\u662F\uFF0C\u4E94\u4E2D\u73B0\u5728\u4E5F\u5C5E\u4E8E\u6C11\u529E\u521D\u4E2D\uFF0C\u8861\n\u6C34\u7684\u5176\u4ED6\u521D\u4E2D\uFF0C\u6BD4\u5982\u56DB\u4E2D\u3001\u4E03\u4E2D\u8FD9\u4E9B\u90FD\u662F\u201C\u7247\u513F\u4E2D\u201D\uFF0C\u9700\u8981\u8861\u6C34\u6237\u53E3\uFF0C\u4F60\u7684\u6237\u53E3\u5212\u5728\u4E86\u54EA\u4E00\u7247\u513F\uFF0C\u5C31\u53BB\u54EA\u6240\u5B66\u6821\u4E0A\u5B66\uFF0C\u4E14\u90FD\u662F\u8D70\u8BFB\u3002</span></p><p><span class="bjh-p">\n\u4F46\u4E94\u4E2D\u4E0D\u540C\uFF0C\u5B83\u4F5C\u4E3A\u8861\u6C34\u5E02\u533A\u975E\u7247\u533A\u5212\u5206\u7684\u5B66\u6821\uFF0C\u4E0D\u53D7\u6237\u53E3\u5F71\u54CD\uFF0C\u5B66\u6821\u5B9E\u884C\u6309\u7167\u5206\u6570\u81EA\u4E3B\u62DB\u751F\uFF0C\u8FD9\u4E9B\u56E0\u7D20\u53E0\u52A0\u8D77\u6765\uFF0C\u4E94\u4E2D\u5438\u5F15\u4E86\u592A\u591A\u60F3\u8981\u4E0A\u8861\u4E2D\u7684\u4EBA\uFF0C\u7279\u522B\u662F\u5916\u5730\u4EBA\u3002</span>\n</p><div class="img-container">\n</div><p>\u8861\u6C34\u4E94\u4E2D\u653E\u5047</p><p><span class="bjh-p">\u4E2D\u8003\u524D\u7684\u4E8C\u6A21\u7ED3\u675F\uFF0C\u4E94\u4E2D\u653E\u5047\u3002\u8FD9\u6240\u5B66\u6821\u5B9E\u884C\u5BC4\u5BBF\u5236\uFF0C\u6BCF\u6B21\u653E\u5047\u7684\u65F6\u5019\uFF0C\u95E8\u53E3\u6574\u6761\u8DEF\u6324\u6EE1\u4E86\u79C1\n\u5BB6\u8F66\uFF0C\u5F88\u591A\u65F6\u5019\u751A\u81F3\u8981\u51FA\u52A8\u4EA4\u8B66\u6765\u7EF4\u6301\u4EA4\u901A\uFF0C\u6574\u4E2A\u573A\u9762\u5982\u540C\u8F66\u5C55\u4E00\u822C\uFF0C\u5404\u79CD\u8F66\u578B\u3001\u5404\u5730\u8F66\u724C\u7684\u90FD\u6709\uFF0C\u9664\u4E86\u8861\u6C34\u672C\u5730\u8F66\u4E4B\u5916\uFF0C\u4F60\u51E0\u4E4E\u80FD\u5728\u8FD9\u91CC\u627E\u5230\u6CB3\u5317\u7701\u5176\u4ED6\n\u57CE\u5E02\u7684\u6240\u6709\u8F66\u8F86\u3002</span></p><p><span class="bjh-p">\u4ECA\u5E74\u521A\u4ECE\u4E94\u4E2D\u521D\u4E2D\u90E8\u6BD5\u4E1A\u7684\u5415\u658C\u8BF4\uFF0C\u4ED6\u4EEC\u73ED\u4E0A\u5916\u5730\u5B66\u751F\u6BD4\u672C\u5730\u4EBA\u8FD8\u591A\uFF0C\u90AF\u90F8\u3001\u4FDD\u5B9A\u3001\u5ECA\u574A\u3001\u6CA7\u5DDE\n\u6765\u7684\u5B66\u751F\u90FD\u6709\u3002\u5982\u679C\u4ECE\u6574\u4E2A\u5B66\u6821\u6765\u770B\u7684\u8BDD\uFF0C\u672C\u5730\u4E0E\u5916\u5730\u5B66\u751F\u7684\u6BD4\u4F8B\u5927\u6982\u662F\u4E00\u534A\u4E00\u534A\uFF0C<span class="bjh-strong">\u6709\u4E9B\u73ED\u4E0A\u751A\u81F3\u8FD8\u6709\u4E00\u4E9B\u5176\u4ED6\u7701\u4EFD\u7684\u4EBA\uFF0C\u6BD4\u5982\u5E7F\u4E1C\u3001\n\u9ED1\u9F99\u6C5F\u3001\u6D77\u5357</span>\u3002</span></p><p><span class="bjh-p">\u73B0\u5728\u7684\u4E94\u4E2D\u6709\u56DB\u4E2A\u6821\u533A\uFF0C\u8FD8\u6709\u4E00\u4E2A\u65B0\u6821\u533A\u6B63\u5728\u5EFA\u8BBE\u4E2D\uFF0C\u5415\u658C\u90A3\u4E00\u5C4A\uFF0C\u56DB\u4E2A\u6821\u533A\u7684\u4EBA\u52A0\u8D77\u67653000\u591A\uFF0C\u4E0B\u4E00\u5C4A4000\u591A\u4EBA\uFF0C\u73B0\u5728\u4E0A\u521D\u4E8C\u7684\u8FD9\u4E00\u5C4A\uFF0C6000\u591A\u4EBA\u3002\u540C\u4E00\u65F6\u671F\uFF0C\u4E03\u4E2D\u5927\u6982\u662F600\u2014700\u4EBA\uFF0C\u56DB\u4E2D\u5219\u53EA\u6709400\u591A\u5B66\u751F\u3002</span></p><div class="img-container">\n</div><p>\u4E94\u4E2D\u6821\u56ED\u6F14\u8BB2\u6BD4\u8D5B</p><p><span class="bjh-p">\u6709\u4E9B\u8D85\u51FA\u610F\u6599\u7684\u662F\uFF0C\u7ADF\u7136\u8FD8\u6709\u4E0D\u5C11\u5317\u4EAC\u7684\u5B66\u751F\u4E5F\u6765\u8FD9\u91CC\u8BFB\u4E66\u3002\u5415\u658C\u73ED\u4E0A110\u591A\u4EBA\uFF0C\u5C31\u67094\u30015\u4E2A\u5317\u4EAC\u7684\u5B66\u751F\n\uFF0C\u4ED6\u8BF4\u4ED6\u4EEC\u73ED\u7B2C\u4E00\u540D\u5C31\u662F\u5317\u4EAC\u7684\uFF0C\u201C\u5BB6\u91CC\u6709\u4F01\u4E1A\u3001\u571F\u8C6A\u3001\u9AD8\u5BCC\u5E05\u3001\u5B66\u4E60\u8FD8\u597D\u3001\u7269\u5316\u8BED\u6587\u82F1\u8BED\u90FD\u5E74\u7EA7\u7B2C\u4E00\u201D\u3002<span class="bjh-strong">\u8FD9\u4E9B\u5317\u4EAC\u5B66\u751F\u4EEC\u7684\u8DEF\u7EBF\u57FA\u672C\u90FD\u662F\u5BB6\n\u91CC\u65E9\u5C31\u89C4\u5212\u597D\u7684\uFF0C\u521D\u4E2D\u5728\u4E94\u4E2D\u4E0A\uFF0C\u4E4B\u540E\u53BB\u8861\u4E2D\u8BFB\u9AD8\u4E2D\uFF0C\u9AD8\u8003\u7684\u65F6\u5019\u8F7B\u8F7B\u677E\u677E\u5C31\u80FD\u8003\u56DE\u5317\u4EAC\u975E\u5E38\u597D\u7684\u5927\u5B66\u3002</span></span></p><p><span class="bjh-p">\u5982\u679C\u672C\u6765\u7684\u6237\n\u53E3\u4E0D\u50CF\u201C\u5317\u4EAC\u201D\u3001\u201C\u5929\u6D25\u201D\u8FD9\u4E48\u6709\u4F18\u52BF\uFF0C\u8FD8\u6709\u4E0D\u5C11\u5BB6\u957F\u4F1A\u7279\u5730\u4E70\u8861\u6C34\u7684\u623F\u5B50\uFF0C\u7ED9\u5B69\u5B50\u843D\u6237\u6210\u8861\u6C34\u6237\u53E3\uFF0C\u56E0\u4E3A\u5728\u4E94\u4E2D\u8BFB\u521D\u4E2D\uFF0C\u867D\u7136\u201C\u8861\u6C34\u6237\u53E3\u201D\u4E0D\u662F\u5FC5\u987B\u6761\u4EF6\uFF0C\u4F46\u662F\u8BFB\u9AD8\u4E2D\u7684\u8BDD\uFF0C\u60F3\u8981\n\u5728\u8861\u6C34\u4E2D\u5B66\u672C\u90E8\uFF0C\u5219\u5FC5\u987B\u8981\u662F\u8861\u6C34\u6237\u53E3\u3002</span></p><div class="img-container">\n</div><p>\u8861\u6C34\u4E94\u4E2D\u4E2D\u8003\u5149\u8363\u699C</p><p><span class="bjh-p">\u201C2017\u5E74\u6843\u57CE\u533A\u4E2D\u8003\u524D10\u540D\uFF0C\u4E94\u4E2D\u53608\u540D\uFF08\u88F8\u5206\u524D10\u540D\uFF0C\u4E94\u4E2D\u53609\u540D\uFF09\uFF0C\u524D20\u540D\u536012\u540D\uFF0C\u524D50\u540D\u536033\u540D\uFF0C\u524D100\n\u540D\u536066\u540D\uFF0C\u524D200\u540D\u5360130\u540D\uFF0C550\u5206\u4EE5\u4E0A946\u4EBA\uFF0C500\u5206\u4EE5\u4E0A1625\u4EBA\u3002\u5168\u533A\u8FD4\u8FD8\u6307\u680726\u4EBA\uFF0C\u5168\u90E8\u88AB\u8BE5\u6821\u6536\u5165\u56CA\u4E2D\u3002\u5168\u533A\u7EDF\u62DB110\u4EBA\uFF0C\u8BE5\u6821\u593A\u5F9785\u4EBA\u3002\u8BE5\u6821\u8861\u4E2D\u53CA\u8861\u4E00\u4E2D\n\u4E0A\u7EBF\u4EBA\u6570\u9AD8\u8FBE603\u4EBA\u3002\u201D</span></p><p><span class="bjh-p">\u5728\u8FD9\u6837\u8F89\u714C\u7684\u6210\u7EE9\u4E0B\uFF0C\u60F3\u4E0A\u4E94\u4E2D\u8BFB\u521D\u4E2D\uFF0C\u96BE\u5EA6\u51E0\u4E4E\u548C\u4E0A\u8861\u4E2D\u8BFB\u9AD8\u4E2D\u76F8\u5F53\u3002\u5415\u658C\u90A3\u4E00\u5C4A\uFF0C\u8861\u6C34\u4E94\u4E2D\u5C0F\u5347\u521D\n\u62A5\u540D\u4EBA\u657015000\u4EBA\uFF0C\u6700\u7EC8\u5F55\u53D6\u4E0D\u52304000\u4EBA\u3002</span></p><p><span class="bjh-p">\u4E94\u4E2D\u5165\u5B66\u7684\u65F6\u5019\u6709\u81EA\u5DF1\u7684\u5165\u5B66\u8003\u8BD5\uFF0C\u8BED\u6587\u6570\u5B66\u4E24\u95E8\uFF0C\u4E3A\u4E86\u6709\u533A\u5206\u68AF\u5EA6\uFF0C\u8003\u8BD5\u9898\u96BE\u5EA6\n\u548C\u77E5\u8BC6\u5BBD\u5EA6\u90FD\u6BD4\u8F83\u5927\uFF0C\u4E00\u822C\u516D\u5E74\u7EA7\u7684\u5B69\u5B50\u5F88\u96BE\u5E94\u5BF9\uFF0C\u8FD8\u7531\u6B64\u51FA\u73B0\u4E86\u4E0D\u5C11\u4E13\u95E8\u5E94\u5BF9\u4E94\u4E2D\u8003\u8BD5\u7684\u8865\u4E60\u73ED\u3002</span></p><div class="img-container">\n</div><p>\u4E94\u4E2D\u5B66\u751F\u4E0A\u8BFE</p><p><span class="bjh-p">\u5415\u658C\u8BF4\uFF0C\u516C\u8D39\u7684\u8BDD\uFF0C\u4ED6\u4EEC\u5F53\u65F6\u5B66\u8D39\u662F\u4E00\u5E743000\u591A\uFF0C\u4E0D\u8FC7\u73B0\u5728\u5DF2\u7ECF\u6DA8\u5230\u4E86\u4E00\u5E746000\uFF0C\u81EA\u8D39\u751F\u5219\u662F\u4E00\u5E749000\uFF0C\u5982\u679C\u81EA\u8D39\u7684\u5206\u6570\u4E5F\u6CA1\u6709\u8FBE\u5230\uFF0C\u5C31\u8981\u6839\u636E\u5206\u6570\u5DEE\u8DDD\u6765\u4EA4\u94B1\uFF0C\u5415\u658C\u81EA\u5DF1\u77E5\u9053\u7684\uFF0C\u6700\u591A\u6709\u4EBA\u4E00\u5E74\u5B66\u8D39\u662F\u4E00\u4E07\u4E8C\u3002<span class="bjh-strong">\u4ED6\u8FD8\u542C\u8BF4\uFF0C\u5728\u8FD9\u51E0\u79CD\u65B9\u6CD5\u4E4B\u5916\uFF0C\u8FD8\u6709\u4E0D\u5C11\u4E70\u623F\u7684\u201C\u540D\u989D\u751F\u201D\uFF0C\u610F\u601D\u662F\u5728\u6307\u5B9A\u7684\u623F\u5730\u4EA7\u9879\u76EE\u4E70\u623F\uFF0C\u5C31\u53EF\u4EE5\u201C\u8D60\u9001\u201D\u4E94\u4E2D\u540D\u989D\u3002</span></span></p><p><span class="bjh-p">\u672C\u6765\u5BB6\u95E8\u53E3\u6709\u7740\u8FD9\u4E48\u597D\u7684\u6559\u80B2\u8D44\u6E90\uFF0C\u81EA\u5DF1\u7684\u5B69\u5B50\u4EEC\u5374\u4EAB\u53D7\u4E0D\u5230\uFF0C\u751A\u81F3\u8FD8\u5F71\u54CD\u5230\u4E86\u521D\u4E2D\u4E0A\u5B66\uFF0C\u9762\u5BF9\u8FD9\u79CD\u60C5\u51B5\uFF0C\u4E0D\u5C11\u8861\u6C34\u672C\u5730\u7684\u5B66\u751F\u5BB6\u957F\u4EEC\u90FD\u975E\u5E38\u751F\u6C14\uFF0C\u4ED6\u4EEC\u4E5F\u8BD5\u8FC7\u53BB\u627E\u8861\u6C34\u6709\u5173\u90E8\u95E8\u53CD\u6620\uFF0C\u4F46\u5F80\u5F80\u6700\u540E\u90FD\u4E0D\u4E86\u4E86\u4E4B\u3002</span></p><p><span class="bjh-p">13\u7EA7\u6BD5\u4E1A\u751F\u5F20\u6708\u56DE\u5FC6\u8BF4\uFF0C\u5979\u4EEC\u90A3\u4E2A\u65F6\u5019\uFF0C\u8861\u4E2D\u3001\u4E8C\u4E2D\u5C31\u56E0\u4E3A\u4EAE\u773C\u7684\u6210\u7EE9\uFF0C\u5438\u5F15\u4E86\u4E0D\u5C11\u5916\u5730\u5B66\u6821\u8FC7\u6765\u53C2\u89C2\u5B66\u4E60\uFF0C\u4E5F\u6709\u4E0D\u5C11\u5916\u5730\u5B66\u751F\u8FC7\u6765\u4E0A\u5B66\uFF0C\u53EF\u662F\u5BF9\u4E8E\u672C\u5730\u4EBA\u6765\u8BF4\uFF0C\u52AA\u52AA\u529B\u8003\u4E0A\u8861\u4E2D\u3001\u4E8C\u4E2D\u5E76\u6CA1\u6709\u8FD9\u4E48\u96BE\uFF0C\u6CA1\u60F3\u5230\u624D\u8FC7\u4E86\u8FD9\u4E48\u51E0\u5E74\uFF0C\u539F\u672C\u4EE4\u8861\u6C34\u4EBA\u9A84\u50B2\u7684\u4F18\u8D28\u5B66\u6821\u8D44\u6E90\uFF0C\u7ADF\u7136\u6210\u4E86\u8861\u6C34\u5B66\u751F\u7684\u201C\u8D1F\u62C5\u201D\uFF0C\u8BA9\u4ED6\u4EEC\u53C8\u7231\u53C8\u6068\u3002</span></p><p><span class="bjh-p"><span class="bjh-strong">\u201C\u6709\u7684\u65F6\u5019\uFF0C\u5012\u662F\u5B81\u613F\u8861\u6C34\u6CA1\u6709\u8FD9\u4E48\u597D\u7684\u9AD8\u4E2D\u201D\uFF0C\u5F20\u6708\u6709\u4E9B\u65E0\u5948\u5730\u8BF4\u3002</span>\uFF08\u4E3A\u4FDD\u62A4\u4E2A\u4EBA\u9690\u79C1\uFF0C\u6587\u4E2D\u5F53\u4E8B\u4EBA\u5747\u4E3A\u5316\u540D\uFF09</span></p><p><span class="bjh-p">\u53C2\u8003\u6587\u732E\uFF1A</span></p><p><span class="bjh-p">[1] \u725B\u5176\u660C.\u4E89\u8BAE\u4E2D\u7684\u8861\u6C34\u7CFB\u201C\u8D85\u7EA7\u4E2D\u5B66\u201D\uFF1A\u62E9\u6821\u5927\u6218\u8513\u5EF6\u81F3\u201C\u5C0F\u5347\u521D\u201D[N].\u754C\u9762.2017-07-10.</span></p><p><span class="bjh-p">[2] \u674E\u82F1\u5F3A,\u90ED\u741B.\u8861\u6C34\u4E00\u4E2D\u518D\u8C03\u67E5\uFF1A\u88AB\u6307\u69A8\u53D6\u4F18\u8D28\u751F\u6E90\uFF0C\u62DB\u751F\u7981\u4EE4\u96BE\u6321\u5B66\u751F\u6D41\u5165[N].Btime\u66B4\u98CE\u773C.2017-07-04.</span></p><p><span class="bjh-p">[3] \u96F7\u78CA,\u85CF\u747E.\u8861\u6C34\u4E2D\u5B66\u201C\u5C01\u795E\u201D\u4E4B\u8DEF\u2014\u2014\u8D85\u7EA7\u9AD8\u8003\u5DE5\u5382[N].\u5357\u65B9\u5468\u672B.2013-10-10</span></p><p><span class="bjh-p">\u672C\u6587\u8F6C\u81EA\u5FAE\u4FE1\u516C\u4F17\u53F7\u201C\u4E0A\u6D41UpFlow\u201D\uFF0C\u4F5C\u8005\u6768\u96C5\u840D\uFF0C\u7F16\u8F91\u5446\u7F8A\u3002\u6587\u7AE0\u4E3A\u4F5C\u8005\u72EC\u7ACB\u89C2\u70B9\uFF0C\u4E0D\u4EE3\u8868\u82A5\u672B\u5806\u7ACB\u573A\u3002</span></p></div>'});/* harmony default export */ __webpack_exports__["a"] = (function(){var state=arguments.length>0&&arguments[0]!==undefined?arguments[0]:defaultState;var action=arguments[1];switch(action.type){default:return state;}});

